.task {
    width: 95%;
    margin: 1% auto;
    background-color: rgb(255, 245, 255);
    border-radius: 10px;
    cursor: pointer;
}

.task div {
    font-size: 0.9rem;
}
.task:hover {
    box-shadow: 2px 2px 4px rgb(122, 90, 122);
    border-radius: 10px;
}

.task_heading {
    height: 20%;
    width: 100%;
    border-radius: 10px;
    display: flex;
    justify-content: space-between;
    padding: 1% 2%;
    align-items: center;
    margin-top: 2%;
}

.high_priority_head {
    background-color: rgba(255, 168, 146, 0.685);
    box-shadow: inset 0 -10px 30px rgba(255, 151, 109, 0.699),
                0 5px 10px rgba(44, 0, 44, 0.493);
}

.medium_priority_head {
    background-color: rgba(243, 231, 125, 0.678);
    box-shadow: inset 0 -10px 30px rgba(245, 213, 32, 0.678),
                0 5px 10px rgba(44, 0, 44, 0.493);
}

.low_priority_head {
    background-color: rgba(255, 194, 255, 0.678);
    box-shadow: inset 0 -10px 30px rgba(255, 136, 255, 0.678),
                0 5px 10px rgba(44, 0, 44, 0.493);
}

.task_deadline {
    margin-right: 0.8%;
    margin-left: 0.7%;
    color: red;
    font-weight: 500;
    width: 12%;
}

.bar {
    height: 20px;
    width: 2px;
    background-color: rgba(0, 0, 0, 0.308);
    margin-right: 2.1%;
}


.task_proj_name {
    margin-right: 2.3%;
    margin-left: 0.7%;
    /* width: 7%; */
}

.task_title {
    color: #0e538c;
    margin-right: 10%;
    flex : 50%;
    /* width: 80%; */
    width: 100px;
    white-space: nowrap;  
    overflow: hidden;
    text-overflow: ellipsis; 
}

.show_full_task_title {
    color: #0e538c;
    margin-right: 10%;
    flex : 50%;
    width: 100px;
}

.task_date {
    /* flex : 1vw; */
    /* margin-left: 100px; */
    margin-right: 0.9%;
}

.task_content {
    width: 100%;
    border-radius: 0 0 10px 10px;
    justify-content: space-around;
    align-items: center;
    padding: 1% 2%;
    text-decoration: none;
}

.high_priority_content {
    background-color: rgba(255, 148, 106, 0.3);
}

.medium_priority_content {
    background-color: rgba(255, 253, 112, 0.3);
}

.low_priority_content {
    background-color: rgba(255, 149, 255, 0.3);
}

.striked {
    text-decoration: line-through;
}

.assigned_name {
    font-weight: 500;
}


.functions {
    display: flex;
    flex-direction: row-reverse;
    justify-content: flex-start;
}

